eval("\n\nwindow.addEventListener('DOMContentLoaded', function () {\n  var header = document.querySelector('.header');\n  var wrapper = document.querySelector('.wrapper');\n  var headerMenu = document.querySelector('.header__menu');\n  var headerBurger = document.querySelector('.header__burger');\n  var registerBtn = document.querySelector('.header__register');\n  var headerActions = document.querySelector('.header__actions');\n  var headerBtns = document.querySelector('.header__btns');\n  var scrollWidth = window.innerWidth - wrapper.offsetWidth + 'px'; //Header Menu\n\n  function headerMenuActions() {\n    if (!/iPhone|iPad|iPod|Android/i.test(navigator.userAgent)) {\n      if (headerMenu.classList.contains('active')) {\n        document.body.style.paddingRight = scrollWidth;\n        header.style.paddingRight = scrollWidth;\n        document.body.classList.add('hidden');\n      } else {\n        document.body.classList.remove('hidden');\n        document.body.style.paddingRight = '';\n        header.style.paddingRight = '';\n      }\n    }\n  }\n\n  headerBurger.addEventListener('click', function () {\n    headerBurger.classList.toggle('active');\n    headerMenu.classList.toggle('active');\n    headerMenuActions();\n    var ariaLabel = headerBurger.getAttribute('aria-label');\n\n    if (ariaLabel === 'Открыть меню') {\n      headerBurger.setAttribute('aria-label', 'Закрыть меню');\n    } else {\n      headerBurger.setAttribute('aria-label', 'Открыть меню');\n    }\n  });\n  document.addEventListener('keydown', function (e) {\n    if (e.code === 'Escape') {\n      removeClasses();\n    }\n  });\n\n  function removeClasses() {\n    if (headerMenu.classList.contains('active')) {\n      headerMenu.classList.remove('active');\n      headerMenuActions();\n    }\n\n    if (headerBurger.classList.contains('active')) {\n      headerBurger.classList.remove('active');\n    }\n  }\n\n  document.addEventListener('click', function (e) {\n    var target = e.target;\n\n    if (target.classList.contains('header__link')) {\n      e.preventDefault();\n      removeClasses();\n    }\n  }); //Active Header\n\n  function headerOnScroll() {\n    if (window.scrollY > 0) {\n      header.classList.add('active');\n    } else {\n      header.classList.remove('active');\n    }\n  } //Header Dinamic Adaptiv\n\n\n  function dinamicAdaptiv() {\n    var viewportWidth = Math.max(document.documentElement.clientWidth, window.innerWidth);\n\n    if (viewportWidth <= 768) {\n      headerActions.append(registerBtn);\n    } else {\n      headerBtns.prepend(registerBtn);\n    }\n  }\n\n  window.addEventListener('resize', function () {\n    dinamicAdaptiv();\n  });\n  window.addEventListener('scroll', function () {\n    headerOnScroll();\n  });\n  window.addEventListener('load', function () {\n    dinamicAdaptiv();\n    headerOnScroll();\n  });\n});\n\n//# sourceURL=webpack://gulpstarter/./src/js/script.js?");

